















































import Vue from "vue";
import {
    BaseInputSearch,
    BaseResultCard,
    BaseResultCardLoading,
    BaseContainer,
    BaseRow,
    BaseColumn,
} from "@/components";

interface Result {
    Title: string;
    Year: string;
    imdbID: string;
    Type: "series" | "movie" | "game";
    Poster: string;
}

interface Response {
    Response: "True" | "False";
    Search: Result[];
    totalResults: string;
}

const results: Result[] = [];

export default Vue.extend({
    components: {
        BaseColumn,
        BaseContainer,
        BaseInputSearch,
        BaseResultCard,
        BaseResultCardLoading,
        BaseRow,
    },
    computed: {
        thereIsResultsToShow() {
            return this.results.length > 0;
        },
    },
    data() {
        return {
            search: "",
            loading: false,
            results,
        };
    },
    methods: {
        async showMoviesThatMatchSearch() {
            if (!this.search) {
                return;
            }

            await this.setSearchResults();
        },
        async setSearchResults() {
            this.loading = true;

            const response = await fetch(
                `http://www.omdbapi.com/?s=${this.search}&apikey=c7e9e5e0`
            );
            const results: Response = await response.json();

            console.log("results", results);

            if (results.Response === "True") {
                this.results = results.Search;
            } else {
                this.results = [];
            }

            this.loading = false;
        },
    },
});
